body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #121212;
  color: #ffffff;
  padding: 0 10%;
}

.App {
  text-align: center;
  padding: 2rem;
}

.intro-section {
  margin-bottom: 2rem;
}

h1 {
  font-size: 2.5rem;
  color: #bbbbbb;
}

.apod-section {
  background-color: #1c1c1c;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.7);
  max-width: 800px;
  margin: 0 auto;
  text-align: left;
  position: relative;
}

.apod-section h2 {
  color: #f3f3f3;
  margin-bottom: 1rem;
  text-indent: 20px;
}

.apod-section img {
  max-width: 100%;
  max-height: 500px;
  width: auto;
  height: auto;
  border-radius: 5px;
  display: block;
  margin: 0 auto;
}

.apod-section iframe {
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  height: 450px;
  border-radius: 5px;
}

.apod-section p {
  margin-top: 1rem;
  color: #d3d3d3;
  text-align: justify;
    line-height: 1.6;
}

.date-picker {
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0.5rem;
  background-color: #1c1c1c;
  color: #ffffff;
  border: 1px solid #444;
  border-radius: 5px;
  font-size: 1rem;
  max-width: 200px;
  text-align: center;
}
